



































export default {
	data(){
		return {
			list:[]
		}
	},
	methods:{
		init(){
			var _this = this;
			uni.request({
				url: this.userUrl+"/BtCApi/List/GetSeriesList", //仅为示例，并非真实接口地址。
				method:'GET',
				success: (res) => {
					console.log(res.data);
					if(res.data.status == true){
						_this.list = res.data.data.item_data;
					}else{
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 2000
						});
					}
				}
			});
		}
	},
	mounted(){
		this.init();
	}
}
